import {BaseFilterElement, type Suggestion} from '../filter-input'
import {controller} from '@github/catalyst'
import {fetchSafeDocumentFragment} from '@github-ui/fetch-utils'

const selectors: {[key: string]: string} = {
  actor: 'ul.js-user-suggestions',
  user: 'ul.js-user-suggestions',
  operation: 'ul.js-operation-suggestions',
  org: 'ul.js-org-suggestions',
  action: 'ul.js-action-suggestions',
  repo: 'ul.js-repo-suggestions',
  country: 'ul.js-country-suggestions ',
}

const debounce = (
  func: (document: Document, url: RequestInfo, options?: RequestInit | undefined) => Promise<DocumentFragment>,
  wait: number,
) => {
  let timerID = -1

  return async function (document: Document, url: RequestInfo, options?: RequestInit | undefined) {
    clearTimeout(timerID)

    const promiseForFunc = new Promise<void>(resolve => {
      timerID = window.setTimeout(resolve, wait)
    })

    await promiseForFunc
    return func(document, url, options)
  }
}

@controller
class AuditLogFilterElement extends BaseFilterElement {
  getSuggestionsUrl(): string {
    return this.searchInput.getAttribute('data-suggestions-path') || ''
  }

  override fetchSuggestionsForQualifier(qualifier: string): Promise<Suggestion[]> {
    qualifier = qualifier.replace(/^-/, '')
    return auditMenuItems(this.getSuggestionsUrl(), qualifier, this.searchFilter())
  }

  // This method iterates over all the terms in the query, and returns a list
  // of any that are invalid because they contain an invalid qualifier.
  override invalidSearchTerms() {
    const validQualifiers = this.fetchQualifierSuggestions().map(q => q.value)
    validQualifiers.push('and')
    validQualifiers.push('AND')
    validQualifiers.push('or')
    validQualifiers.push('OR')

    const parts = this.searchInput.value.split(/\s+/)

    const invalidParts = parts.filter(part => {
      part = part.replace(/^-/, '')
      const colonIndex = part.indexOf(':')

      if (colonIndex === -1) {
        // if hasn't typed a ':' yet, assume valid if any
        // of the valid qualifiers start with what's typed so far
        return !validQualifiers.some(validQualifier => validQualifier.startsWith(part))
      } else {
        // If we have typed a ':', it must fully match a valid qualifier
        const typedQualifier = part.substr(0, colonIndex + 1)

        return !validQualifiers.some(validQualifier => validQualifier === typedQualifier)
      }
    })

    if (invalidParts.length === 0) {
      return null
    }

    return invalidParts.join(' ')
  }

  searchFilter(): string {
    const searchString = this.searchInput.value || ''
    const splits = searchString.split(':')
    const filter = splits.length > 1 ? splits[1] : ''
    return filter || ''
  }

  async handleInputFocusIn(): Promise<void> {
    await auditMenuItems(this.getSuggestionsUrl(), '', this.searchFilter())
    this.updateFilterSuggestionResults()
  }
}

async function auditMenuItems(url: string, key: string, filter: string): Promise<Array<{value: string}>> {
  const lists = await fetchAuditMenu(url, filter)
  const list = lists.querySelector<HTMLElement>(selectors[key]!)
  if (!list) return []
  const listItems = list.querySelectorAll<HTMLElement>('li')
  return [...listItems].map(li => ({value: li.getAttribute('data-value')!, description: li.textContent?.trim()}))
}

const debouncedFetchSafeDocumentFragment = debounce(fetchSafeDocumentFragment, 200)

async function fetchAuditMenu(url: string, filter: string): Promise<HTMLElement> {
  const fragment = await debouncedFetchSafeDocumentFragment(document, `${url}?query=${encodeURIComponent(filter)}`)
  const root = document.createElement('div')
  root.append(fragment)
  return root
}
